import { graphql } from 'gatsby'
import React from 'react'
import Header from "../components/header/header"
import HeaderImage from "../components/headerSection/headerSection"
import Footer from "../components/footer/footer"
import Post from "../components/post/post"
import { Helmet } from "react-helmet"

export const query = graphql
    `
    query MdxBlogPost ($id: String) {
        mdx(
            id: { eq: $id }
        ){
            body
            frontmatter {
                title
                description
                image {
                    childImageSharp {
                      fluid(quality: 100) {
                        ...GatsbyImageSharpFluid_withWebp_noBase64
                      }
                    }
                }
            }
        }
    }
`

export default ({ data }) => {
    if (data !== undefined){
        const { frontmatter, body } = data.mdx
        return (
            <>
                <Helmet>
                    <html lang="en" />
                    <title>{frontmatter.title + ' - Lewis Campbell'}</title>
                    <meta name="description" content={frontmatter.description} />
                    <link rel="canonical" href="https://lewismcampbell.com" />
                    <meta property="og:image" content={'https://lewismcampbell.com' + frontmatter.image.childImageSharp.fluid.src} />
                    <meta property="og:description" content={frontmatter.description} />
                    <meta property="og:title" content={frontmatter.title + ' - Lewis Campbell'} />
                    <meta name="twitter:card" content="summary_large_image" />
                    <meta name="twitter:site" content="@lewismcampbell" />
                    <meta name="twitter:creator" content="@lewismcampbell" />
                    <meta name="twitter:title" content={frontmatter.title + ' - Lewis Campbell'}/>
                    <meta name="twitter:description" content={frontmatter.description} />
                    <meta name="twitter:image" content={'https://lewismcampbell.com' + frontmatter.image.childImageSharp.fluid.src} />
                </Helmet>
                <Header />
                <HeaderImage image={frontmatter.image.childImageSharp.fluid} title={frontmatter.title} description={frontmatter.description} />
                <Post body={body} />
                <Footer />
            </>
        )
    } else {
        return (<Header />)
    }
}